import React, { Component } from 'react'
import "./index.scss"
import { NavLink } from 'react-router-dom'
import logo from '../../assets/logo.webp'

export default class NavBar extends Component {
    render() {
        return (
            <div className='nav-bar'>
                <div className='left'>
                    <NavLink to="/" className='logo'>
                        <img src={logo}></img>
                    </NavLink>
                    <div className='menu'>
                        <NavLink to="home" className='menu-item'>Home</NavLink>
                        <NavLink to="blog" className='menu-item'>Blog</NavLink>
                    </div>
                </div>
                <div className='right'>
                    <div className='menu'>
                        <NavLink to="about" className='menu-item'>About</NavLink>
                        <NavLink to="contact" className='menu-item'>Contact</NavLink>
                    </div>
                </div>
            </div>
        )
    }
}
